import React from "react";
import styled from 'styled-components';
import NavItem from './NavItem';

const NavRailDiv = styled.ul`
  display: flex;
  align-items: center;
  list-style-type: none;
  margin: 0px;
  padding: 0px;
`;

const NavRail = () => {
  return (
    <NavRailDiv>
      <NavItem toId={'intro'}>Hi</NavItem>
      <NavItem toId={'experience'}>Experience</NavItem>
      <NavItem toId={'project'}>Project</NavItem>
      <NavItem toId={'skill'}>Skill</NavItem>
    </NavRailDiv>
  )
}


export default NavRail;